














import { Component, Vue } from 'vue-property-decorator'
import { Chapter1 } from '@/hycal_lib/chapter1'

import HycalForm from '@/components/HycalForm/index.vue'

@Component({
  components: {
    HycalForm
  }
})
export default class Cpt1_1_2 extends Vue {
  private title = '1.1.2宽顶堰泄流能力';
  private initData = {
    v_0: 3,
    H: 4.8,
    '\\varepsilon': 0.977,
    n: 2,
    b: 11,
    P_1: 5.2,
    方角: false
  };
  private data = {
    v_0: '',
    H: '',
    '\\varepsilon': '',
    n: '',
    b: '',
    P_1: '',
    方角: false
  };
  private formulas = {
    1: '$$Q :流量,m^3/s$$',
    2: '$$m :流量系数$$',
    3: '$$\\varepsilon :侧收缩系数$$',
    5: '$$n :孔数$$',
    4: '$$b :单孔宽度,m$$',
    6: '$$H :堰上水头,m,取堰前3H_{0}～6H_{0}处$$',
    7: '$$v_0 :行进流速,m/s$$',
    8: '$$g :重力加速度,m/s^2,取9.8$$',
    9: '$$P_1 :上游堰高,m$$'
  };

  private demoContent =
    '某水利工程水库校核水位615m,溢洪道进水口控制堰为宽顶堰,堰长20.5m,进口底板高程605m,堰顶高程610.2m,进口底坎边缘用半径0.8m圆弧修圆,2 孔,单孔宽度11m,闸墩厚度1.5m,eplison ＝0.977,计算控制堰泄流能力。';
  private demoResult = '求得控制堰泄流能力为428.73';
  get result() {
    let objs = Chapter1.kdy(
      +this.data.v_0,
      +this.data.H,
      +this.data['\\varepsilon'],
      +this.data.n,
      +this.data.b,
      +this.data.P_1,
      this.data.方角
    )
    let outStr = '流量系数m:'
    outStr +=
      objs.m.toFixed(2).toString() +
      '|泄流能力Q:' +
      objs.Q.toFixed(2).toString()
    return outStr
  }
}
